
import { defineComponent, inject, onMounted, PropType, ref, SetupContext } from 'vue'
import { ComponentSize, componentSizeArr } from '@/base/types'
import { useModelWatch } from '@/base/hooks'
import { Emitter } from 'mitt'

interface InputProps {
  modelValue: string,
  type: string,
  size: ComponentSize,
  prefixIcon: string,
  suffixIcon: string,
  clear: boolean,
  password: boolean,
  round: boolean,
  autoFocus: boolean,
}

export default defineComponent({
  name: 'FInput',
  props: {
    // v-model
    modelValue: {
      type: String as PropType<string>,
      default: '',
      required: true
    },
    // type
    type: {
      type: String as PropType<string>,
      default: 'text'
    },
    // 
    size: {
      type: String as PropType<ComponentSize>,
      default: 'default',
      validator (value: string): boolean {
        return componentSizeArr.includes(value)
      }
    },
    // icon
    prefixIcon: {
      type: String as PropType<string>,
      default: ''
    },
    // icon
    suffixIcon: {
      type: String as PropType<string>,
      default: ''
    },
    // 
    clear: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 
    password: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 
    round: {
      type: Boolean as PropType<boolean>,
      default: false
    },
    // 
    autoFocus: {
      type: Boolean as PropType<boolean>,
      default: false
    }
  },
  emits: ['update:modelValue', 'input', 'blur', 'focus'],
  setup (props: InputProps, ctx: SetupContext) {
    // 
    const inputValue = ref<string>(props.modelValue)
    useModelWatch(() => props.modelValue, inputValue, ['update:modelValue', 'input'])
    // input
    const inputDom = ref<HTMLElement | undefined>(undefined)
    // 
    const inputType = ref<string>(props.type)
    // formItem mitt 
    const formItemMitt = inject<Emitter['emit'] | undefined>('formItemMitt', undefined)

    // 
    const clearInputClick = (): void => {
      inputValue.value = ''
      if (inputDom.value) inputDom.value.focus()
    }
    // /
    const showHidePasswordClick = (): void => {
      inputType.value = inputType.value === 'password' ? 'text' : 'password'
    }
    // 
    const inputBlur = (): void => {
      if (formItemMitt) formItemMitt('formItemBlur', inputValue.value)
      ctx.emit('blur', inputValue.value)
    }

    onMounted(() => {
      //  
      if (props.password) inputType.value = 'password'
      // formItem
      if (formItemMitt) formItemMitt('formItemValue', inputValue.value)
      // 
      setTimeout(() => {
        if (props.autoFocus && inputDom.value) inputDom.value.focus()
      })
    })

    return {
      inputValue,
      inputType,
      inputDom,
      inputBlur,
      clearInputClick,
      showHidePasswordClick
    }
  }
})
